import loadErr from '@/components/loadErr.vue'

export default {
	data() {
		return {
			navColor: '#E80214',//导航背景色
			isLoadErr: false, //页面加载出错
			dataObj: {}, //数据对象
			id: null, //订单id
		}
	},
	components: { loadErr },
	computed: {
		pageMinH() {
			let sys = uni.getSystemInfoSync()
			return sys.windowHeight + 'px'
		}
	},
	onLoad(opt) {
		this.id = opt.id
		this.getDetailData()
	},
	methods: {
		toRelaodData() {
			this.isLoadErr = false
			this.dataObj = {}
			this.getDetailData()
		},
		getDetailData() {
			uni.showLoading({
				title: '数据加载中',
				mask: true
			})
			this.$api.ajax({
				url: this.$config.getOrderDetail,
				data: {
					order_id: this.id
				}
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					this.dataObj = res.data.data
					setTimeout(() => {
						uni.hideLoading();
					}, 500)
				}else {
					uni.hideLoading();
					this.$api.warnNotice(res.data.message)
					this.isLoadErr = true
				}
			})
			.catch(() => {
				uni.hideLoading();
				this.isLoadErr = true
			})
		}
	}
}
